import axios from 'axios';

let base = '';
/*登录*/
export const requestLogin = params => { return axios.post(`${base}/login`, params); };

export const getlogin = params => { return axios.post(`/apiDev/enterprise/login`, params); };
/*商品管理页-商品列表*/
export const commodityList = params => { return axios.get(`${base}/`, { params: params }); };
/*商品管理页-商品名称模糊查询*/
export const commoditySearchName = params => { return axios.post(`${base}/`, params ); };
/*商品管理页-商品用友编码模糊查询*/
export const commoditySearchCode = params => { return axios.post(`${base}/`, params ); };
/*商品管理页-商品app类目*/
export const commodityTypeList = params => { return axios.post(`${base}/`, params ); };
/*商品管理页-条件检索商品列表*/
export const commoditySearch = params => { return axios.post(`${base}/`, params ); };
/*商品管理页-编辑公共-商品信息内容*/
export const coCommodityInfo = params => { return axios.post(`${base}/`, params ); };
/*商品管理页-编辑-上传图片地址*/
export const uploadImg = params => { return axios.post(`${base}/`, params ); };
/*商品管理页-编辑公共-保存商品信息*/
export const saveCoCommodity = params => { return axios.post(`${base}/`, params ); };
/*商品管理页-编辑规格-商品信息内容*/
export const tyCommodityInfo = params => { return axios.post(`${base}/`, params ); };
/*商品管理页-编辑规格-保存商品信息*/
export const saveTyCommodity = params => { return axios.post(`${base}/`, params ); };
/*商品管理页-编辑规格-保存商品详情*/
export const saveTyCommodityDetail = params => { return axios.post(`${base}/`, params ); };
/*商品管理页-编辑规格-保存商品内页图*/
export const saveTyCommodityInimg = params => { return axios.post(`${base}/`, params ); };
/*订单管理-订单列表*/
export const orderList = params => { return axios.post(`${base}/`, params ); };
/*订单管理-订单号精确搜索*/
export const orderSearchCode = params => { return axios.post(`${base}/`, params ); };
/*订单管理-条件查询订单列表*/
export const orderSearch = params => { return axios.post(`${base}/`, params ); };
/*订单管理-订单详情-订单信息*/
export const orderInfo = params => { return axios.post(`${base}/`, params ); };
/*订单管理-订单详情-保存备注信息*/
export const saveRemarks = params => { return axios.post(`${base}/`, params ); };


