import login from '../views/Login.vue'
import notFound from '../views/404.vue'
import home from '../views/Home.vue'
const order = () => import ('../views/order/');
const orderdetail = () => import ('../views/order/children/detail.vue');
const commodity = () => import ('../views/commodity/');
const coedit = () => import ('../views/commodity/children/coedit.vue');
const tyedit = () => import ('../views/commodity/children/tyedit.vue');

let routes = [
    {
        path: '/login',
        component: login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: notFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: home,
        name: '妙商城',
        children: [
            { 
                path: '/commodity', 
                component: commodity, 
                name: '商品管理' ,
            },
            {
                path: '/commodity/coedit', 
                component: coedit, 
                name: '编辑公共信息',
                hidden: true,
            },
            {
                path: '/commodity/tyedit', 
                component: tyedit, 
                name: '编辑规格信息' ,
                hidden: true,
            },
            { 
                path: '/order',
                component: order, 
                name: '订单管理' ,
            },
            {
                path: '/order/detail', 
                component: orderdetail, 
                name: '订单详情' ,
                hidden: true,
            },
            
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


